import React, { Component, lazy, Suspense } from "react";
import { Link,NavLink } from 'react-router-dom';
import {
  Carousel,
  CarouselItem,
  Col, 
  Row,
  Card,
  CardBody,
  CarouselCaption,
  CardHeader,
  CarouselIndicators,
  CarouselControl,
  Button,
  CardFooter
} from "reactstrap";

const items = [
  {
    src:
      "data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%22800%22%20height%3D%22400%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%20800%20400%22%20preserveAspectRatio%3D%22none%22%3E%3Cdefs%3E%3Cstyle%20type%3D%22text%2Fcss%22%3E%23holder_1607923e7e2%20text%20%7B%20fill%3A%23555%3Bfont-weight%3Anormal%3Bfont-family%3AHelvetica%2C%20monospace%3Bfont-size%3A40pt%20%7D%20%3C%2Fstyle%3E%3C%2Fdefs%3E%3Cg%20id%3D%22holder_1607923e7e2%22%3E%3Crect%20width%3D%22800%22%20height%3D%22400%22%20fill%3D%22%23777%22%3E%3C%2Frect%3E%3Cg%3E%3Ctext%20x%3D%22285.9296875%22%20y%3D%22217.75625%22%3EFirst%20slide%3C%2Ftext%3E%3C%2Fg%3E%3C%2Fg%3E%3C%2Fsvg%3E",
    altText: "Slide 1",
    caption: "Slide 1"
  },
  {
    src:
      "data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%22800%22%20height%3D%22400%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%20800%20400%22%20preserveAspectRatio%3D%22none%22%3E%3Cdefs%3E%3Cstyle%20type%3D%22text%2Fcss%22%3E%23holder_15ba800aa20%20text%20%7B%20fill%3A%23444%3Bfont-weight%3Anormal%3Bfont-family%3AHelvetica%2C%20monospace%3Bfont-size%3A40pt%20%7D%20%3C%2Fstyle%3E%3C%2Fdefs%3E%3Cg%20id%3D%22holder_15ba800aa20%22%3E%3Crect%20width%3D%22800%22%20height%3D%22400%22%20fill%3D%22%23666%22%3E%3C%2Frect%3E%3Cg%3E%3Ctext%20x%3D%22247.3203125%22%20y%3D%22218.3%22%3ESecond%20slide%3C%2Ftext%3E%3C%2Fg%3E%3C%2Fg%3E%3C%2Fsvg%3E",
    altText: "Slide 2",
    caption: "Slide 2"
  },
  {
    src:
      "data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%22800%22%20height%3D%22400%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%20800%20400%22%20preserveAspectRatio%3D%22none%22%3E%3Cdefs%3E%3Cstyle%20type%3D%22text%2Fcss%22%3E%23holder_15ba800aa21%20text%20%7B%20fill%3A%23333%3Bfont-weight%3Anormal%3Bfont-family%3AHelvetica%2C%20monospace%3Bfont-size%3A40pt%20%7D%20%3C%2Fstyle%3E%3C%2Fdefs%3E%3Cg%20id%3D%22holder_15ba800aa21%22%3E%3Crect%20width%3D%22800%22%20height%3D%22400%22%20fill%3D%22%23555%22%3E%3C%2Frect%3E%3Cg%3E%3Ctext%20x%3D%22277%22%20y%3D%22218.3%22%3EThird%20slide%3C%2Ftext%3E%3C%2Fg%3E%3C%2Fg%3E%3C%2Fsvg%3E",
    altText: "Slide 3",
    caption: "Slide 3"
  }
];

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = { activeIndex: 0,
      cardData : [] ,
      temp : null,
      isLoading : false
    };
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
    this.goToIndex = this.goToIndex.bind(this);
    this.onExiting = this.onExiting.bind(this);
    this.onExited = this.onExited.bind(this);
  }

  async componentDidMount(){
    this.setState({isLoading:true});
    // await fetch(`http://localhost:8081/blog/getOne/22`)
    // .then(res => res.json())
    // .then(json => { 
    //     let cardData = json; 
    //     this.setState({cardData});
    //      var t = cardData.body;
    //     // var temp =  t.split('>').pop().split('<')[0];
    //      var temp = t.split(/(><)/)[0];
    //      var temp = temp + '>';
    //      this.setState({temp});
    //      console.log(t);
    //     console.log(temp);
    // });

    await fetch(`http://localhost:8081/getAllPost`)
    .then(res => res.json())
    .then(json => { 
        let cardData = json; 
        this.setState({cardData});
         //var t = cardData.body;
         console.log(cardData);
         // var temp =  t.split('>').pop().split('<')[0];
        //  var temp = t.split(/(><)/)[0];
        //  var temp = temp + '>';
        //  this.setState({temp});
        //  console.log(t);
        // console.log(temp);
    })

  }

  onExiting() {
    this.animating = true;
  }

  onExited() {
    this.animating = false;
  }

  next() {
    if (this.animating) return;
    const nextIndex =
      this.state.activeIndex === items.length - 1
        ? 0
        : this.state.activeIndex + 1;
    this.setState({ activeIndex: nextIndex });
  }

  previous() {
    if (this.animating) return;
    const nextIndex =
      this.state.activeIndex === 0
        ? items.length - 1
        : this.state.activeIndex - 1;
    this.setState({ activeIndex: nextIndex });
  }

  goToIndex(newIndex) {
    if (this.animating) return;
    this.setState({ activeIndex: newIndex });
  }
  render() {
    // if(!this.state.isLoading)
    // {
    //   return(<div>Loading ... </div>)
    // }
    const { activeIndex } = this.state;
    var {cardData} = this.state;
    const slides =items.map((item) => {
        return (
          <CarouselItem
            onExiting={this.onExiting}
            onExited={this.onExited}
            key={item.src}
          >
            <img className="d-block w-100" src={item.src} alt={item.altText} />
            <CarouselCaption captionText={item.caption} captionHeader={item.caption} />
          </CarouselItem>
        );
      });

    return (
      
      <div className="animated fadeIn">
        <Row>
        <Col>     
                <Carousel activeIndex={activeIndex} next={this.next} previous={this.previous}>     
                  <CarouselIndicators items={items} activeIndex={activeIndex} onClickHandler={this.goToIndex} />
                  {slides}
                  <CarouselControl direction="prev" directionText="Previous" onClickHandler={this.previous} />
                  <CarouselControl direction="next" directionText="Next" onClickHandler={this.next} />
                </Carousel>
              
          </Col>
        </Row>
        {/* ==============Get experts tips area===================== */}
        <Card></Card>
        <Card>
        <CardHeader className="text-center text-md-center">  Get Experts Tips and Advice  </CardHeader>
      <CardBody> 
      <Row>
      <Col xs="12" sm="6" md="3">
          <Card>
              <CardHeader>
                  Your Health
              </CardHeader>
              <CardBody>
              </CardBody>
          </Card>
      </Col>
      <Col xs="12" sm="6" md="3">
          <Card>
              <CardHeader>
                  Baby's Health
              </CardHeader>
              <CardBody>
              </CardBody>
          </Card>
      </Col>
      <Col xs="12" sm="6" md="3">
          <Card>
              <CardHeader>
                  Baby's Sleep
              </CardHeader>
              <CardBody>
              </CardBody>
          </Card>
      </Col>
      <Col xs="12" sm="6" md="3">
          <Card>
              <CardHeader>
                  Baby's Food
              </CardHeader>
              <CardBody>
              </CardBody>
          </Card>
      </Col>
      </Row>
      </CardBody>
      </Card>
      {/* ============== End area ===================== */}

       {/* ==============Others people experience area===================== */}
       <Card></Card>
        <Card>
        <CardHeader className="text-center text-md-center">  What are others people experiences  </CardHeader>
      <CardBody> 
      <Row>

      {
        this.state.cardData.map(forEach => 
          <Col xs="12" sm="6" md="3">
            <Card>
            <CardHeader>
              {forEach.user.name}
            </CardHeader>
            <CardBody>
              <div> <h5> {forEach.title} </h5></div>
              <div dangerouslySetInnerHTML={ { __html: forEach.body.split(/(><)/)[0] } }></div>
            </CardBody>
            <CardFooter>
            {/* <Link to={{ pathname: '/details_post',  state: { key: forEach.id, data: forEach} }}>Continue reading ...</Link> */}
            <NavLink to="/details_post" className="nav-link" >Home</NavLink>
            </CardFooter>
            </Card>
            
          </Col>

          )
      }
      </Row>
      </CardBody>
      </Card>
      {/* ============== End area ===================== */}
      </div>
    );
  }
}
export default Home;
